.work {
  flex-direction: column;
  margin: 0 auto;
  display: flex;
}

.work__content {
  padding: 30px 16px 0;
}

.work__img-container {
  grid-template-rows: 2fr 2fr 2fr 2fr 2fr;
  grid-template-columns: 160px 160px;
  justify-content: center;
  margin: 0 auto;
  display: grid;
}

.work__img-container-item {
  object-fit: cover;
  width: 160px;
  height: 152px;
}

.work__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.work-item-four {
  object-fit: cover;
  grid-area: 5 / 1 / auto / span 3;
  width: 320px;
}

.work-item-three {
  grid-area: 4 / 2;
}

.work-item-two, .work-item-six {
  grid-column: 1;
}

.work-item-five {
  grid-area: 1 / 2 / span 4;
  height: 456px;
}

.work-item-seven {
  grid-column: 1;
}

.work__img-wrapper {
  position: relative;
}

.work__img-overlay {
  color: #fff;
  opacity: 0;
  background: linear-gradient(360deg, #edd789e6, #e98283e6);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Raleway, sans-serif;
  font-size: 14px;
  transition: opacity .5s ease-in-out;
  display: flex;
  position: absolute;
  inset: 0;
}

.work__img-title {
  text-transform: uppercase;
  margin: 40px 0 0;
  padding: 0;
  font-size: 18px;
  font-weight: 700;
}

.work__img-svg {
  width: 28px;
  height: 28px;
}

@media (width >= 768px) {
  .work {
    padding: 0;
  }

  .work__content {
    padding: 58px 0 42px;
  }

  .work__text {
    max-width: 792px;
  }

  .work__img-container {
    grid-template-rows: repeat(2, 256px);
    grid-template-columns: repeat(auto-fit, minmax(100px, 256px));
    max-width: 1024px;
  }

  .work__img-overlay:hover {
    opacity: 1;
  }

  .work__img-container-item {
    width: 256px;
    height: 256px;
  }

  .work-item-five {
    grid-area: 1 / 3 / span 3;
    height: 512px;
  }

  .work-item-four {
    grid-area: 2 / 2 / span 3;
    max-width: 256px;
  }

  .work-item-three {
    grid-area: 1 / 2;
  }

  .work-item-seven, .work-item-six {
    grid-column: 4;
  }

  .work-item-seven {
    grid-row: 1;
  }
}

@media (width >= 1300px) {
  .work__content {
    padding: 84px 0 42px;
  }

  .work__text {
    max-width: 1170px;
  }

  .work__img-container {
    grid-template-rows: repeat(2, 378px);
    grid-template-columns: repeat(auto-fit, minmax(100px, 378px));
    max-width: 1500px;
  }

  .work__img-container-item {
    width: 378px;
    height: 378px;
  }

  .work-item-five {
    grid-area: 1 / 3 / span 3;
    height: 756px;
  }

  .work-item-four {
    max-width: 378px;
  }

  .work__img-subtitle {
    font-size: 18px;
  }

  .work__img-title {
    margin: 58px 0 0;
    font-size: 24px;
  }

  .work__img-svg {
    width: 42px;
    height: 42px;
  }
}
